body {
  background:  #e0e0e0
}

.list__tile__avatar {
     width: 54px;
 }

/* dialog 控件在不显示的时候就啥也不显示*/
.dialog__container {
  display: block;
}


code, kbd, pre, samp {
 width: 100%;
  /*background: #282c34;*/
}

/*代码高亮*/

code:after, code:before, kbd:after, kbd:before {
    content: "";
    letter-spacing: -1px;
}
