  .homepage-container {
    width: 100%;
  }
}
@media only screen and (min-width: 960px) {
  .homepage-container {
    width: 100%;
  }
} 
@media only screen and (min-width: 1280px) {
  .homepage-container {
    width: 70%;
  }
} 

